import { Delete, Edit } from '@mui/icons-material';
import { Box, Button, Container, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, Grid, Paper, Stack, Typography } from '@mui/material';
import { deleteCategoryAsyncThunk } from 'features/category/categorySlice';
import { selectCategoryDeleteStatus, selectCategoryError, selectSelectedCategory, selectShopList, setSelectedCategory, setCategoryDeleteStatus, setSelectedShop } from 'features/main/mainSlice';
import { selectAccessToken } from 'features/user/userSlice';
import QRCode from 'qrcode.react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import Iconify from 'template/components/iconify';
import { useRouter } from 'template/routes/hooks';
import NoShopComponent from 'template/sections/shops/noshop/no-shop-component';

export default function CategoryList() {
  const router = useRouter();
  const dispatch = useDispatch();

  // shop တစ်ခုလုံး လိုတယ်။
  const shopList = useSelector(selectShopList);
  const selectedCategory = useSelector(selectSelectedCategory);
  // category နဲ့ ပက်သက်ရင် delete status လိုမယ်။
  const categoryDeleteStatus = useSelector(selectCategoryDeleteStatus);
  const categoryError = useSelector(selectCategoryError);
  // လုပ်ချင်တာ လုပ်ဖို့ access token
  const accessToken = useSelector(selectAccessToken);


  // ---------- internal state ------------ //
  // for delete confirmation dialog
  const [deleteConfirmationDialog, setDeleteConfirmationDialog] = useState(false);


  const categoryEditOnClick = (category) => {
    console.log('categoryEditOnClick');
    console.log(category);
    // navigate to category edit page
    dispatch(setSelectedCategory(category));

    //dispatch(setSelectedMenu(menu));
    router.push(`/categories/${category.id}/edit`);
  };

  const categoryDeleteOnClick = (category) => {
    // delete category
    // show confirmation dialog
    // let formData = new FormData();
    // formData.append('access_token', access_token);
    // formData.append('id', category.id);
    // dispatch(deleteCategoryAsyncThunk(formData));
    dispatch(setSelectedCategory(category));
    setDeleteConfirmationDialog(true);
  };

  const cancelDelete = () => {
    // close dialog
    setDeleteConfirmationDialog(false);
  }
  const confirmDelete = (id, access_token) => {
    // close dialog
    setDeleteConfirmationDialog(false);
    let formData = new FormData();
    formData.append('access_token', access_token);
    formData.append('id', id);
    dispatch(deleteCategoryAsyncThunk(formData));
  }

  const newCategoryClicked = (shop) => {
    console.log("newCategoryClicked");
    console.log(shop.name);
    console.log(shop.id);
    dispatch(setSelectedShop(shop.id));
    console.log("dispatch");
    // go to category new page

    // router.push(`/categories/${1}/edit`);
    router.push('/categories/create');
    console.log("router");
  }

  const handleErrorDialogClose = () => {
    // set error status to idle
    dispatch(setCategoryDeleteStatus('idle'));
  }
  
  return (
    <>
      <Container component="main" maxWidth="lg">
        {shopList.length === 0 ? <NoShopComponent /> : <Container></Container> }
        {shopList.map((shop) => (
          <Box key={shop.id} mb={4}>
            {/* shop heading list */}
            <Grid container>
              <Grid item lg={2} xs={3} textAlign={'center'}>
                <img
                  alt={'shop info'}
                  src={shop.shop_image_url ?? 'https://via.placeholder.com/350x350'}
                />
              </Grid>

              <Grid item lg={8} xs={6} textAlign={'center'}>
                <Typography variant="h2">{shop.name}</Typography>
                <Typography p={2}>{shop.description}</Typography>
                <Typography>{shop.address}</Typography>
              </Grid>
              <Grid item lg={2} xs={3} textAlign={'center'}>
                <QRCode value={`https://qrmenu.mmsoftware100.com/${shop.url}`} size={100} />
                {/* <img alt={'qr code'} src={'https://via.placeholder.com/350x350'} /> */}
              </Grid>
            </Grid>

            {/* categories header */}
            <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5} mt={5}>
              <Typography variant="h4">Categories</Typography>

              <Button
                onClick={() => newCategoryClicked(shop) }
                // component={Link}
                // to={`/categories/create?shop_id=${shop.id}`}
                variant="contained"
                color="inherit"
                startIcon={<Iconify icon="eva:plus-fill" />}
              >
                New Category
              </Button>
            </Stack>

            {/* categories list */}
            <Grid container spacing={2} mt={4}>
              {shop.categories.map((category) => (
                <Grid xs={'6'} lg={3} item>
                  <Grid
                    container
                    direction={'column'}
                    sx={{ border: '1px solid grey', borderRadius: '4px' }}
                  >
                    <Grid
                      item
                      textAlign={'center'}
                      p={2}
                      sx={{ backgroundColor: 'grey', color: 'white' }}
                    >
                      {category.name}
                    </Grid>
                    <Grid container>
                      <Grid item xs={6} textAlign={'center'}>
                        {' '}
                        <Button onClick={() => categoryEditOnClick(category)}>
                          <Edit />
                        </Button>
                      </Grid>
                      <Grid item xs={6} textAlign={'center'}>
                        {' '}
                        <Button onClick={() => categoryDeleteOnClick(category) }>
                          <Delete />
                        </Button>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              ))}
            </Grid>

            <br></br>
            <Divider />
          </Box>
        ))}
      </Container>



      {/* delete confirmation dialog */}
      <Dialog
        open={deleteConfirmationDialog}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Delete Confirmation!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete {selectedCategory.name} ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => cancelDelete(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => confirmDelete(selectedCategory.id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>

      {/* delete error dialog */}
      <Dialog
        open={categoryDeleteStatus === 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Error!</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">{categoryError}</DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
